.contact {
  background: var(--primary-black);
  position: relative;
}

.contact::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, var(--primary-gold), transparent);
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: start;
}

.contact-info {
  background: rgba(255, 215, 0, 0.05);
  border: 1px solid rgba(255, 215, 0, 0.1);
  border-radius: 20px;
  padding: 2.5rem;
  text-align: left;
  transition: all 0.3s ease;
}

.contact-info h3 {
  color: var(--primary-gold);
  font-size: 2rem;
  margin-bottom: 1rem;
  font-weight: 600;
}

.contact-info p {
  color: var(--text-gray);
  font-size: 1.1rem;
  margin-bottom: 2rem;
}

.contact-social {
  display: flex;
  gap: 1.5rem;
  margin-top: 1rem;
}

.contact-social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  background: rgba(255, 215, 0, 0.1);
  border: 2px solid var(--primary-gold);
  border-radius: 50%;
  color: var(--primary-gold);
  font-size: 1.2rem;
  text-decoration: none;
  transition: all 0.3s ease;
}

.contact-social-link:hover {
  background: var(--primary-gold);
  color: var(--primary-black);
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(255, 215, 0, 0.3);
}

.contact-form {
  background: rgba(255, 215, 0, 0.05);
  border: 1px solid rgba(255, 215, 0, 0.1);
  border-radius: 20px;
  padding: 2.5rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  transition: all 0.3s ease;
}

.contact-form input,
.contact-form textarea {
  background: rgba(0, 0, 0, 0.7);
  border: 1px solid rgba(255, 215, 0, 0.2);
  border-radius: 10px;
  color: var(--text-white);
  font-size: 1rem;
  padding: 1rem;
  outline: none;
  transition: border 0.3s;
  resize: none;
}

.contact-form input:focus,
.contact-form textarea:focus {
  border: 1.5px solid var(--primary-gold);
}

.contact-form .btn {
  margin-top: 1rem;
}

.form-success {
  color: var(--primary-gold);
  font-size: 1.2rem;
  text-align: center;
  padding: 2rem 0;
}

@media (max-width: 900px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  .contact-info, .contact-form {
    padding: 2rem;
  }
}

@media (max-width: 480px) {
  .contact-info, .contact-form {
    padding: 1rem;
  }
  .contact-info h3 {
    font-size: 1.5rem;
  }
  .contact-social-link {
    width: 40px;
    height: 40px;
    font-size: 1rem;
  }
  .form-success {
    font-size: 1rem;
    padding: 1rem 0;
  }
} 